import React from "react";
import Grid from "@mui/material/Grid";

export default function OfferingList(props) {
  const { header, subtitle, body, image, color } = props.l;

  return (
    <Grid container className="offeringClass">
      <Grid className="grid" item xs={12} md={6}>
        <div className="paddingRight">
        <h5 className={!color ? "gradientClass" : ""} style={{ color: color }}>
          {header}
        </h5>
        <h2>{subtitle}</h2>
        {props.width > 900 && <p>{body}</p>}
        </div>
      </Grid>

      <Grid className="grid" item xs={12} md={6}>
        <img src={image} alt={header} />
        {props.width < 900 && <p>{body}</p>}
      </Grid>
    </Grid>
  );
}
